.card {
  background-color: #fff;
  border: 1px solid rgb(212, 212, 213);
  border-radius: 5px;
  padding: 6px;
  min-width: 300px;
  margin-top: 12px;
}

.card__label {
  font-size: 14px;
  margin-bottom: 6px;
}

.card__meta {
  font-size: 12px;
}

.card__meta__link {
  text-decoration: none;
  color: #5352ed;
}

.note__form {
  margin-top: 12px;
}

.note__form__textarea {
  border: 1px solid rgb(212, 212, 213);
  border-radius: 5px;
  padding: 6px;
  min-width: 311px;
  margin-top: 6px;
}

.column {
  border: 1px solid rgb(212, 212, 213);
  border-radius: 5px;
  padding: 12px;
  margin: 12px;
  min-width: 325px;
  background-color: #dfe6e9;
}

.column--dashed {
  min-width: 319px;
  background-color: #ffffff;
  max-height: 200px;
  border: dashed 3px rgb(212, 212, 213);
  padding: 20px;
  display: inline-block;
}

.icon {
  cursor: pointer;
  font-size: 20px;
  margin-left: 6px;
  width: 24px;
}

.icon:hover {
  color: #5352ed;
}

.column__panel {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: space-between;
          justify-content: space-between;
}

.column__panel__header {
  margin-top: 3px;
  font-size: 18px;
  font-weight: bold;
}

.column__panel__label {
  text-align: center;
  display: inline-block;
  border-radius: 50%;
  width: 22px;
  height: 22px;
  background-color: #5352ed;
  color: #ffffff;
}

.column__panel__label--text {
  font-size: 14px;
}

.column__panel--left {
  display: -webkit-flex;
  display: flex;
}

.column__panel--right {
  display: -webkit-flex;
  display: flex;
}

.board-container {
  display: -webkit-flex;
  display: flex;
}

.column__placeholder {
  display: -webkit-flex;
  display: flex;
  padding-top: 15%;
  -webkit-justify-content: center;
          justify-content: center;
}

.App {
  text-align: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 80px;
}

.App-header {
  background-color: #222;
  height: 150px;
  padding: 20px;
  color: white;
}

.App-title {
  font-size: 1.5em;
}

.App-intro {
  font-size: large;
}

@-webkit-keyframes App-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}

@keyframes App-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}

body {
  margin: 0;
  padding: 0;
  font-family: sans-serif;
}

.button {
  cursor: pointer;
  height: 28px;
  border-radius: 5px;
  font-size: 14px;
  border: 0;
}

.button--primary {
  background-color: #5352ed;
  color: #ffffff;
}

.button--primary:hover {
  background-color: #3742fa;
  color: #ffffff;
}

.button--postive {
  color: #fff;
  background-color: #2ed573;
}

.button--postive:hover {
  color: #fff;
  background-color: #7bed9f;
}

.button--neutral {
  background-color: #ffffff;
}

.button--neutral:hover {
  background-color: #f1f2f6;
}

.button--bold {
  font-weight: bold;
}

.button__group {
  padding-top: 3px;
  display: -webkit-flex;
  display: flex;
}

.button__group > button {
  width: 100%;
}

.button__group > button:first-child {
  margin-right: 6px;
}

